        week: 'Week',
        day: 'Day',
        '4day': '4 Days',
      },
      selectedEvent: {},
      selectedElement: null,
      selectedOpen: false,
      events: [],
      
     
    }),
    mounted () {
     this.$refs.calendar.checkChange()
       
    },
    methods: {
      viewDay ({ date }) {
        this.focus = date
        this.type = 'day'
