      },
      next () {
        this.$refs.calendar.next()
      },
      showEvent ({ nativeEvent, event }) {
        // this.$router.push('/app/tech/myjob/'+event.id);
        console.log(event)
          alert("Installer Name: " + event.installer + "\nBranch: " + event.br);
      
      },
     async updateRange ({ start, end }) {
         const res = await this.$store.dispatch('app_booking_sys/calendarDATA');

        // Update the events after the asynchronous call is complete
        this.events = res.data;

        // The following line will now log the events after they have been updated
        console.log(this.events);
      },
      rnd (a, b) {
        return Math.floor((b - a + 1) * Math.random()) + a
      },
    },
  }
</script>

